// Class 28 Assignment

// General Styles
*{
  box-sizing: border-box;
}
.content{
  background: #28abb9;
  text-align: center;
  margin-bottom: 10px;
  h2{
    padding: 10px;
    color: #2d6187;
  }
}
fieldset{
  position: relative;
  background-color: #ff69b414;
  height: 300px;
  padding: 10px;
  margin-bottom: 30px;
  legend{
    font-size: 20px;
    font-family: sans-serif;
    padding: 0px 10px;
  }
}



// Draggable
.main-container{
  height: 100%;
  #draggable{
    width: 150px;
    height: auto;
    text-align: center;
    display: inline-block;
    padding: 10px;
    background: turquoise;
    border-radius: 5px;
    cursor: move;
  }
}

// Drag & Drop
.dragAndDrop{
  height: 100%;
  .draggable{
    width: 170px;
    background-color: #d63031;
    padding: 15px;
    border-radius: 5px;
    display: inline-block;
    text-align: center;
    color: #ffffff;
    cursor: move;
    z-index: 999;
  }
  #other{
    background-color: blue !important;
  }
  .droppable{
    position: absolute;
    right: 10px;
    top: 0;
    width: 300px;
    height: 230px;
    background-color: #2d3436;
    padding: 15px;
    border-radius: 5px;
    display: inline-block;
    text-align: center;
    color: #ffffff;
  }
  .ui-state{
    background-color: green;
  }
}
